import "./poemStyle.css";

import {useRef, useState, useEffect} from "react";
import {motion, useViewportScroll, useTransform} from "framer-motion";

export default function Poem(props) {
    const textArr = [
        <>
            <p>От добрых слов ты слышишь вновь немало солидарности,</p>
            <p>Но никому не выразить речами благодарности.</p>
            <p>Ведь человек не книга, дело и без слова твёрдое,</p>
            <p>И за хорошее придёшь ты в руки распростёртые.</p>
        </>,
        <>
            <p>Куда свернуть, не выбрал путь, другие неприятности -</p>
            <p>Домой пора бы заглянуть, где нет к тебе халатности,</p>
            <p>Где мама скажет без привычной миру страшной жадности:</p>
            <p>Куда свернуть, как выбрать путь и сбросить неприятности.</p>
        </>
    ]
    const reactText = textArr.map((text, index) => <PoemQuatrain text={text} key={index}/>)
    return (
        <section className={"poemSection"}>
            <div className={"poemContainer"}>
                <PoemHeader/>
                {reactText}
            </div>
        </section>
    )
}

function PoemQuatrain(props) {
    const {scrollY} = useViewportScroll()
    const usedRef = useRef(null);
    const [offsetTopSection, setOffsetTop] = useState(null);
    const [offsetHeightSection, setOffsetHeight] = useState(null);
    const isSectionShowed = useTransform(scrollY, (value) => {
        return ((value + document.documentElement.offsetHeight / 4 >= offsetTopSection + 150) && (value + document.documentElement.offsetHeight / 4 <= offsetTopSection + offsetHeightSection + 150)) ? 1 : 0
    });
    useEffect(() => {
        const rect = usedRef.current.getBoundingClientRect();
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        const offsetStart = rect.top + scrollTop;
        setOffsetHeight(rect.height);
        setOffsetTop(offsetStart);
    }, [offsetTopSection])
    return (
        <motion.div
            layout
            ref={usedRef}
            style={{opacity: isSectionShowed}}
        >{props.text}
        </motion.div>
    )
}

function PoemHeader(props) {
    const {scrollY} = useViewportScroll()
    const usedRef = useRef(null);
    const [offsetTopSection, setOffsetTop] = useState(null);
    const [offsetHeightSection, setOffsetHeight] = useState(null);
    const isSectionShowed = useTransform(scrollY, (value) => {
        return ((value + document.documentElement.offsetHeight / 5 >= offsetTopSection + 150) && (value + document.documentElement.offsetHeight / 5 <= offsetTopSection + offsetHeightSection + 150)) ? 1 : 0
    });
    useEffect(() => {
        const rect = usedRef.current.getBoundingClientRect();
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        const offsetStart = rect.top + scrollTop;
        setOffsetHeight(rect.height);
        setOffsetTop(offsetStart);
    }, [offsetTopSection])
    return (
        <motion.h2
            layout
            ref={usedRef}
            style={{opacity: isSectionShowed}}
        >Прочти</motion.h2>
    )
}
