import AP from "../../img/AP.jpg";
import LM from "../../img/LM.jpg";
import quotesBg from "../../img/quotes.svg";
import {useRef, useState, useEffect} from "react";

import "./quoteStyle.css";
import {motion, useTransform, useViewportScroll} from "framer-motion";

export default function Quotes(props) {
    const quotesArr = [
        {
            image: LM,
            quote: "Первое слово человека – мама, последнее – мама. Мир держится на ласке матерей.",
            author: "Михаил Лезинский"
        },
        {
            image: AP,
            quote: "Сердце матери видит дальше всех и чует лучше, чем чье-либо другое сердце.",
            author: "Аркадий Первенцев"
        }
    ]
    const reactQuotes = quotesArr.map((data, index) => <Quote data={data} key={index} index={index}/>)

    const {scrollY} = useViewportScroll()
    const usedRef = useRef(null);
    const [offsetTopSection, setOffsetTop] = useState(null);
    const [offsetHeightSection, setOffsetHeight] = useState(null);
    const isSectionShowed = useTransform(scrollY, (value) => {
        return ((value + document.documentElement.offsetHeight / 5 >= offsetTopSection + 150) && (value + document.documentElement.offsetHeight / 5 <= offsetTopSection + offsetHeightSection + 150)) ? 1 : 0
    });
    useEffect(() => {
        const rect = usedRef.current.getBoundingClientRect();
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        const offsetStart = rect.top + scrollTop;
        setOffsetHeight(rect.height);
        setOffsetTop(offsetStart);
    }, [offsetTopSection])
    return (
        <motion.section
            layout
            ref={usedRef}
            style={{opacity: isSectionShowed}}
            className={"quoteSection"}>
            <div className={"quoteContainer"}>
                <QuotesHeader/>
                {reactQuotes}
            </div>
        </motion.section>
    )
}

function Quote(props) {
    return (
        <div className={"quoteBlock " + (props.index % 2 === 0 ? "left" : "right")}>
            <div className={"quoteContent"}>
                <div className={"photo"}>
                    <img src={props.data.image}/>
                </div>
                <p className={"quote"}>{props.data.quote}</p>
                <span className={"author"}>{props.data.author}</span>
            </div>
            <img className={"quotesBg"} src={quotesBg}/>
        </div>
    )
}

function QuotesHeader(props) {
    const {scrollY} = useViewportScroll()
    const usedRef = useRef(null);
    const [offsetTopSection, setOffsetTop] = useState(null);
    const [offsetHeightSection, setOffsetHeight] = useState(null);
    const isSectionShowed = useTransform(scrollY, (value) => {
        return ((value + document.documentElement.offsetHeight / 4 >= offsetTopSection + 150) && (value + document.documentElement.offsetHeight / 4 <= offsetTopSection + offsetHeightSection + 250)) ? 1 : 0
    });
    useEffect(() => {
        const rect = usedRef.current.getBoundingClientRect();
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        const offsetStart = rect.top + scrollTop;
        setOffsetHeight(rect.height);
        setOffsetTop(offsetStart);
    }, [offsetTopSection])
    return (
        <motion.h2
            layout
            ref={usedRef}
            style={{opacity: isSectionShowed}}
        >Запомни</motion.h2>
    )
}
