import "./headerStyle.css";
import {motion} from "framer-motion";
import instagram from "../img/instagram.png";
import vk from "../img/vk.png";

export default function Header(props) {
    return (
        <motion.header initial={{opacity: 0}} animate={{opacity: 1}} transition={{type: "easeInOut"}}>
            <nav>
                <motion.button
                    onClick={() => {
                        props.setConfettyRun(true);
                        props.setRecycle(false);
                        props.setCongratulate(true);
                    }}
                    animate={{opacity:! props.isCongratulate ? 1 : 0.5}}
                >Поздравить</motion.button>
                <div className={"socialBlock"}>
                    <a href={"https://www.instagram.com/nina.starkova/"} className={"socialButton instagramButton"}>
                        <img src={instagram}/>
                        <span>@nina.starkova</span>
                    </a>
                    <a href={"https://vk.com/nina.starkova"} className={"socialButton vkButton"}>
                        <img src={vk}/>
                        <span>Нина Старкова</span>
                    </a>
                </div>
            </nav>
            <motion.span
                initial={{ clipPath: "inset(100% 0% 0% 0%)" }}
                animate={{clipPath: `inset(0px ${(1 - props.scrollProgress / props.maxScrollProgress) * 100}% 0px 0px`}}
                transition={{type: "easeInOut"}}
                className={"headerScrollProcess"}>{}</motion.span>
        </motion.header>
    )
}
