import "./congratulationStyle.css";
import smile from "../../img/csm.png";
import {motion} from "framer-motion";
import {useState} from "react";

export default function Congratulation(props) {
    const [isButtonHover, setButtonHover] = useState(false);
    return (
        <section className={"congratulationSection"}>
            <motion.div className={"textBlock"} initial={{x: "-100vw"}} animate={{x: "0"}} transition={{duration: 1, type: "spring", delay: 0.5}}>
                <h2>С Днём рождения!!!</h2>
                <p>Любимой маме от всей семьи с наилучшими пожеланиями</p>
                <motion.img animate={{scale: isButtonHover ? 1.5 : 1, rotate: isButtonHover ? 45 : 0}} src={smile}/>
                <motion.button
                    onMouseEnter={() => {
                        setButtonHover(true)
                    }}
                    onMouseLeave={() => {
                        setButtonHover(false)
                    }}
                    onClick={() => {
                        props.setConfettyRun(true);
                        props.setRecycle(false);
                        props.setCongratulate(true);
                    }}
                    animate={{opacity:! props.isCongratulate ? 1 : 0.5,}}
                >Урааа!!!</motion.button>
            </motion.div>
            <div className={"imageBlock"}>
                <motion.div drag  dragConstraints={{ left: 0, right: 0, top: 0, bottom: 0 }} dragTransition={{ bounceStiffness: 600, bounceDamping: 20 }} className={"mainImage"}><span>{}</span></motion.div>
                <div className={"absImage third"}><span>{}</span></div>
                <div className={"absImage fourth"}><span>{}</span></div>
            </div>
        </section>
    )
}
