import "./mainStyle.css";
import Poem from "./poem/Poem";

import Congratulation from "./congratulation/Congratulation";
import Header from "../header/Header";
import Quotes from "./QuoteSection/Quotes";

export default function Main(props) {
    return (
        <main>
            <Congratulation toggleScrollProgress={props.toggleScrollProgress} setConfettyRun={props.setConfettyRun} setRecycle={props.setRecycle}  isCongratulate={props.isCongratulate} setCongratulate={props.setCongratulate}/>
            <Poem/>
            <Quotes/>
        </main>
    )
}
