import instagram from "../img/instagram.png";
import vk from "../img/vk.png";

import "./footerStyle.css";

export default function Footer(props) {
    return (
        <footer>
            <div>
                <a>
                    <a href={"https://www.instagram.com/nina.starkova/"} className={"socialButton instagramButton"}>
                        <img src={instagram}/>
                        <span>@nina.starkova</span>
                    </a>
                    <a href={"https://vk.com/nina.starkova"} className={"socialButton vkButton"}>
                        <img src={vk}/>
                        <span>Нина Старкова</span>
                    </a>
                </a>
            </div>
        </footer>
    )
}
