import './App.css';
import Header from "./header/Header";
import {useState, useEffect} from 'react';
import Main from "./main/Main";
import Footer from "./footer/Footer";
import Confetti from 'react-confetti'
import Background from "./background/Background";

function App() {
    const [scrollProgress, setScrollProgress] = useState(0);
    const [isConfettyRun, setConfettyRun] = useState(false);
    const [isRecycle, setRecycle] = useState(true);
    const [isCongratulate, setCongratulate] = useState(false);
    const [mainImageCoordinates, setMainImageCoordinates] = useState(null);
    const maxScrollProgress = 5;
    useEffect(() => {
        const coords = document.getElementsByClassName("mainImage")[0].getBoundingClientRect();
        setMainImageCoordinates({x: coords.left, y: coords.top, w: coords.width, h: coords.height})
    }, [])

    function toggleScrollProgress(isIncrement) {
        if (isIncrement) {
            if (scrollProgress < maxScrollProgress) {
                setScrollProgress(scrollProgress + 1);
            }
        } else if (scrollProgress > 0) {
            setScrollProgress(scrollProgress - 1);
        }
    }

    return (
        <>
            <Header scrollProgress={scrollProgress} maxScrollProgress={maxScrollProgress}
                    setConfettyRun={setConfettyRun} setRecycle={setRecycle} isCongratulate={isCongratulate}
                    setCongratulate={setCongratulate}/>
            <Main toggleScrollProgress={toggleScrollProgress} setConfettyRun={setConfettyRun} setRecycle={setRecycle}
                  isCongratulate={isCongratulate} setCongratulate={setCongratulate}/>
            <Footer/>
            <Background/>
            <Confetti
                run={isConfettyRun}
                height={document.documentElement.scrollHeight}
                numberOfPieces={400}
                recycle={isRecycle}
                confettiSource={mainImageCoordinates}
            />
        </>
    );
}

export default App;
